<template>
  <div class="hello">
    <div class="position-relative d-flex align-items-center justify-content-center">
      <div class="row">
        <div class="col-12 pb-2">
          <select id="selectedFacet" v-model="selectedPerson">
            <option value="confirmed">Confirmados</option>
            <option value="dead">Mortos</option>
            <option value="recovered">Recuperados</option>
          </select>
        </div>
        <div class="col-12 pb-2">
          <input type="number" id="top10" min="1" max="10" v-model="topX" />
        </div>
        <div class="col-12 pb-2">
          <button @click="getTopCountries()">Visualizar Casos</button>
        </div>
        <div class="col-12 pb-2" v-if="selectedPerson == 'confirmed'">
          <img src="../assets/confirmed.jpg" style="width: 300px; height:auto" />
        </div>
        <div class="col-12 pb-2" v-if="selectedPerson == 'dead'">
          <img src="../assets/Dead.png" style="width: 300px; height:auto" />
        </div>
        <div class="col-12 pb-2" v-if="selectedPerson == 'recovered'">
          <img src="../assets/RECOVERED.jpg" style="width: 300px; height:auto" />
        </div>
        <div class="col-12 pb-2" v-if="date != ''">
          <p>{{ date.getDate() }}/{{ date.getMonth() + 1 }}/{{ date.getFullYear() }}</p>
        </div>
      </div>
    </div>
    <div class="position-relative d-flex align-items-center justify-content-center">
      <div v-if="topCountries.length != 0">
        <table class="table" style="width: 40rem;">
          <thead class="thead-dark">
            <tr>
              <th scope="col">País</th>
              <th scope="col" v-if="selectedPerson == 'confirmed'">Total Confirmados</th>
              <th scope="col" v-if="selectedPerson == 'confirmed'">Novos Confirmados</th>
              <th scope="col" v-if="selectedPerson == 'dead'">Total Mortes</th>
              <th scope="col" v-if="selectedPerson == 'dead'">Novas Mortes</th>
              <th scope="col" v-if="selectedPerson == 'recovered'">Total Recuperados</th>
              <th scope="col" v-if="selectedPerson == 'recovered'">Novos Recuperados</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="c in topCountries" :key="c.Country">
              <td>{{ c.Country }}</td>
              <td v-if="selectedPerson == 'confirmed'">{{ c.TotalConfirmed }}</td>
              <td v-if="selectedPerson == 'confirmed'">{{ c.NewConfirmed }}</td>
              <td v-if="selectedPerson == 'dead'">{{ c.TotalDeaths }}</td>
              <td v-if="selectedPerson == 'dead'">{{ c.NewDeaths }}</td>
              <td v-if="selectedPerson == 'recovered'">{{ c.TotalRecovered }}</td>
              <td v-if="selectedPerson == 'recovered'">{{ c.NewRecovered }}</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "covid19",
  data() {
    return {
      countries: [],
      selectedPerson: "",
      top10: "",
      topCountries: [],
      date: ""
    };
  },
  methods: {
    async getTopCountries() {
      const response = await fetch("https://api.covid19api.com/summary");
      const myData = await response.json();
      // this.covid = myData.Countries.find(
      //   country => country.Country == this.selectedCountry
      // );

      this.topCountries = [];
      this.countries = myData.Countries;

      if (this.selectedFacet == "confirmed") {
        this.countries.sort((a, b) =>
          a.TotalConfirmed < b.TotalConfirmed ? 1 : -1
        );
        for (let i = 0; i < parseInt(this.topX); i++) {
          this.topCountries.push(this.countries[i]);
        }
      } else if (this.selectedFacet == "dead") {
        this.countries.sort((a, b) => (a.TotalDeaths < b.TotalDeaths ? 1 : -1));
        for (let i = 0; i < parseInt(this.topX); i++) {
          this.topCountries.push(this.countries[i]);
        }
      } else if (this.selectedFacet == "recovered") {
        this.countries.sort((a, b) =>
          a.TotalRecovered < b.TotalRecovered ? 1 : -1
        );
        for (let i = 0; i < parseInt(this.topX); i++) {
          this.topCountries.push(this.countries[i]);
        }
      }

      this.date = new Date();
    }
  }
};
</script>